import React from 'react';
import { ButtonToolbar, Button} from 'react-bootstrap';
import FontAwesome from 'react-fontawesome';
import { Link } from 'react-router-dom';
import '../App.css';

class LeadDetails extends React.Component {

	render() {
		const {details, index} = this.props;

		return (
			<div>
			<Link to="/leads">
			<FontAwesome name="arrow-left" />
			</Link>
				
			<div className="lead-details container-fluid">
				<div className=" lead-main-block">
					<div className="pull-right ">
						<ButtonToolbar>
							<Link to={`/converted/${index}`}>
								<Button bsSize="large" bsStyle="primary" onClick={() => this.props.con(index)}>Convert</Button>
							</Link>
							<Button  bsSize="large">Send Email</Button>
							<Link to={`/leads/edit/${index}`}>
							<Button  bsSize="large"  key={index} >Edit</Button>
							</Link>

						</ButtonToolbar>
					</div>
				</div>
					<div className="leaddetails-name ">
						<strong>{details.fname} {details.lname}</strong> - {details.company}
					</div>
				<div className="clearB"></div>
				<div>
					<div className="row mDiv">
						<div className="fixed-width col-md-4">Lead Owner</div> 
						<div className="col-md-4">Praveen Kumar</div>
					</div>
					<div className="row mDiv">
						<div className="fixed-width col-md-4">Email</div> 
						<div className="col-md-4">{details.email}</div>
					</div>
					<div className="row mDiv">
						<div className="fixed-width col-md-4">Phone</div>
					 	<div className="col-md-4">{details.phone} </div>
					 </div>
					<div className="row mDiv">
						<div className="fixed-width col-md-4">Mobile</div> 
						<div className="col-md-4">{details.mobile}</div>
					</div>
					<div className="row mDiv">
						<div className="fixed-width col-md-4">Lead Status</div> 
						<div className="col-md-4">{details.leadstatus} </div>
					</div>
				</div>
				<div className="lspace">
					<div className="lead-info">
						<div className="mDiv"><strong>Lead Information</strong></div>
						<div className="floatL ltab_0">
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Lead Owner</div> 
								<div className="col-md-4">Praveen Kumar</div>
							</div>
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Phone</div>
								<div className="col-md-4">{details.phone}</div>
							</div>
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Mobile</div>
								<div className="col-md-4">{details.mobile}</div>
							</div>
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Lead Source</div>
								<div className="col-md-4">{details.leadsource}</div>
							</div>
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Industry</div>
								<div className="col-md-4">{details.industry}</div>
							</div>
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Annual Revenue</div>
								<div className="col-md-4">{details.revenue}</div>
							</div>
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Company</div>
								<div className="col-md-4">{details.company}</div>
							</div>
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Email</div>
								<div className="col-md-4">{details.email}</div>
							</div>
						</div>
						<div className="floatL ltab_1">
							
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Lead Name</div>
								<div className="col-md-4">{details.fname} {details.lname}</div>
							</div>
							
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Fax</div>
								<div className="col-md-4">{details.fax}</div>
							</div>
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Website</div>
								<div className="col-md-4">{details.website}</div>
							</div>
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Lead Status</div>
								<div className="col-md-4">{details.leadstatus}</div>
							</div>
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">No. of Employees</div>
								<div className="col-md-4">{details.emps}</div>
							</div>
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Skype Id</div>
								<div className="col-md-4">{details.skype}</div>
							</div>
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Twitter</div>
								<div className="col-md-4">{details.twitter}</div>
							</div>
						</div>
					</div>
				<div className="clearB"></div>

					<div className="address-info lspace">
						<div className="mDiv"><strong>Address Information</strong></div>
						<div className="floatL ltab_0">
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Address</div>
								<div className="col-md-4">{details.address}</div>
							</div>
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">State</div>
								<div className="col-md-4">{details.state}</div>
							</div>
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Country</div>
								<div className="col-md-4">{details.country}</div>
							</div>
						</div>
						<div className="floatL ltab_1">
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">City</div>
								<div className="col-md-4">{details.city}</div>
							</div>
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Zip Code</div>
								<div className="col-md-4">{details.zip}</div>
							</div>
						</div>
					</div>
				<div className="clearB"></div>

					<div className="desc-info lspace">
						<div className="mDiv"><strong>Description Information</strong></div>
						
						<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Description</div>
								<div className="col-md-4">{details.desc}</div>
						</div>
						
					</div>
				</div>
			</div>
			</div>
		)
	}
}

export default LeadDetails ;


