import React from 'react';
import { ButtonToolbar, Button} from 'react-bootstrap';
import { Link } from 'react-router-dom';

import FontAwesome from 'react-fontawesome';


class ContactDetails extends React.Component {

	render() {
		const {details} = this.props;
		return (
			<div>
			<Link to="/contacts">
			<FontAwesome name="arrow-left" />
			</Link>
			<div className="contact-details container-fluid">
				<div className="pull-right">
				<ButtonToolbar>
					<Button bsSize="large">Send Email</Button>
					<Button bsSize="large">Edit</Button>
				</ButtonToolbar>
				</div>
				<div className="contactdetails-name">
				<strong>{details.fname} {details.lname}</strong> - {details.aname}
				</div>
				<div className="clearB"></div>

				<div>
					<div className="row mDiv">
						<div className="fixed-width col-md-4">Contact Owner</div> 
						<div className="col-md-4">Praveen Kumar</div>
					</div>
					<div className="row mDiv">
						<div className="fixed-width col-md-4">Email</div> 
						<div className="col-md-4">{details.email}</div>
					</div>
					<div className="row mDiv">
						<div className="fixed-width col-md-4">Phone</div> 
						<div className="col-md-4">{details.phone}</div>
					</div>
					<div className="row mDiv">
						<div className="fixed-width col-md-4">Mobile</div> 
						<div className="col-md-4">{details.mobile}</div>
					</div>
					<div className="row mDiv">
						<div className="fixed-width col-md-4">Department</div> 
						<div className="col-md-4">{details.dep}</div>
					</div>
				</div>
				<div className="contact-info lspace">
					<div className="mDiv"><strong>Contact Information</strong></div>
					<div className="floatL ltab_0">
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Contact Owner</div>
							<div className="col-md-4">Praveen Kumar</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Lead Source</div>
							<div className="col-md-4">{details.leadsource}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Account Name</div>
							<div className="col-md-4">{details.aname}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Contact Name</div>
							<div className="col-md-4">{details.fname} {details.lname}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Email</div>
							<div className="col-md-4">{details.email}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Phone</div>
							<div className="col-md-4">{details.phone}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Other Phone</div>
							<div className="col-md-4">{details.othphone}</div>
						</div>
						
					</div>
					<div className="floatL ltab_1">
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Home Phone</div>
							<div className="col-md-4">{details.homphone}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Mobile</div>
							<div className="col-md-4">{details.mobile}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Fax</div>
							<div className="col-md-4">{details.fax}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Assistant</div>
							<div className="col-md-4">{details.assistant}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Asst Phone</div>
							<div className="col-md-4">{details.asstphone}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Date of Birth</div>
							<div className="col-md-4">{details.dob}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Skype Id</div>
							<div className="col-md-4">{details.skype}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Twitter</div>
							<div className="col-md-4">{details.twitter}</div>
						</div>
						
					</div>
				</div>
				<div className="clearB"></div>

				<div className="address-info lspace">
					<div className="mDiv"><strong>Address Information</strong></div>
					<div className="floatL ltab_0">
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Mailing Address</div>
								<div className="col-md-4">{details.maddress}</div>
							</div>
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Mailing State</div>
								<div className="col-md-4">{details.mstate}</div>
							</div>
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Mailing Country</div>
								<div className="col-md-4">{details.mcountry}</div>
							</div>
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Mailing City</div>
								<div className="col-md-4">{details.mcity}</div>
							</div>
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Mailing Zip Code</div>
								<div className="col-md-4">{details.mzip}</div>
							</div>
						</div>
					<div>
					<div className="floatL ltab_1">
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Other Address</div>
								<div className="col-md-4">{details.oaddress}</div>
							</div>
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Other State</div>
								<div className="col-md-4">{details.ostate}</div>
							</div>
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Other Country</div>
								<div className="col-md-4">{details.ocountry}</div>
							</div>
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Other City</div>
								<div className="col-md-4">{details.ocity}</div>
							</div>
							<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Other Zip Code</div>
								<div className="col-md-4">{details.ozip}</div>
							</div>
						</div>

					</div>
				</div>
				<div className="clearB"></div>
				<div className="desc-info lspace">
						<div className="mDiv"><strong>Description Information</strong></div>
						
						<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Description</div>
								<div className="col-md-4">{details.desc}</div>
						</div>	
				</div>
			</div>
			</div>
		)
	}

}

export default ContactDetails ;


