import React from 'react';
import { Button, ButtonToolbar } from 'react-bootstrap';

class LeadsEdit extends React.Component {
  editLead(event) {
    event.preventDefault();
    const lead = {
      fname: this.fname.defaultValue,
      lname: this.lname.defaultValue,
      company: this.company.defaultValue,
      email: this.email.defaultValue,
      phone: this.phone.defaultValue,
      fax: this.fax.defaultValue,
      mobile: this.mobile.defaultValue,
      website: this.website.defaultValue,
      leadsource: this.leadsource.defaultValue,
      leadstatus: this.leadstatus.defaultValue,
      industry: this.industry.defaultValue,
      emps: this.emps.defaultValue,
      revenue: this.revenue.defaultValue,
      skype: this.skype.defaultValue,
      twitter: this.twitter.defaultValue,
      address: this.address.defaultValue,
      city: this.city.defaultValue,
      state: this.state.defaultValue,
      zip: this.zip.defaultValue,
      country: this.country.defaultValue,
      desc: this.desc.defaultValue,
    }

    this.props.modifyLead(lead);
    //this.leadForm.reset();

  }


  render() {
    const {index} = this.props.match.params;
    const details = this.props.passLead()[index];



    return (
      <div className="lead-edit">
        <div className="lead-header">
          <h1 className="lead"><strong>Edit Lead</strong></h1>
        </div>
      <div className="lead-form">
        <form ref={(input) => this.leadForm = input} className="lead-edit" onSubmit={(e) => this.editLead(e)}>
          <div className="pull-right">
          <ButtonToolbar>
            <Button type="submit"  bsStyle="primary" bsSize="large">Save</Button>
            <Button type="reset" bsStyle="warning" bsSize="large">Cancel</Button>
          </ButtonToolbar>
          </div>

          <div className="lspace">
            <div className="lead-info">
              <div className="mDiv"><strong>Lead Information</strong></div>
              <div className="floatL ltab_0">
                <div className="row pR tabDiv">
                  <div className="fixed-width col-md-4 ">Lead Owner</div> 
                    <div className="col-md-4">
                      <input type="text"  value="Praveen Kumar" readOnly/>
                    </div>
                </div>
                <div className="row pR tabDiv  ">
                  <div className="fixed-width col-md-4 mandatory ">First Name</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.fname = input} type="text"  style={{width: "100%"}} defaultValue={details.fname}/>
                    </div>
                </div>    
                <div className="row pR tabDiv ">
                 <div className="fixed-width col-md-4 mandatory">Last Name</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.lname = input} type="text" defaultValue={details.lname}/>
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 mandatory">Company</div> 
                    <div className="col-md-4">     
                      <input ref={(input) => this.company = input} type="text"  defaultValue={details.company}/>
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 mandatory">Email</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.email = input} type="text"  defaultValue={details.email} />
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Phone</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.phone = input} type="text"  defaultValue={details.phone}/>
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Fax</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.fax = input} type="text"  defaultValue={details.fax}/>
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Mobile</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.mobile = input} type="text"  defaultValue={details.mobile} />
                    </div>
                </div>
                
              </div>
              </div>
              <div className="floatL ltab_1">
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Website</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.website = input} type="text" defaultValue={details.website} />
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Lead Source</div> 
                    <div className="col-md-4">
                      <select ref={(input) => this.leadsource = input} defaultValue={details.leadsource}>
                        <option value="none">None</option>
                        <option value="advertisement">Advertisement</option>
                        <option value="employee-referral">Employee Referral</option>
                        <option value="Partner">Partner</option>
                        <option value="public-relations">Public Relations</option>
                        <option value="trade-show">Trade Show</option>
                        <option value="web-download">Web Download</option>
                        <option value="web-research">Web Research</option>
                        <option value="chat">Chat</option>
                      </select>
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Lead Status</div> 
                    <div className="col-md-4">
                      <select ref={(input) => this.leadstatus = input} defaultValue={details.leadstatus}>
                        <option value="none">None</option>
                        <option value="attempted">Attempted to Contact</option>
                        <option value="future">Contact in Future</option>
                        <option value="contacted">Contacted</option>
                        <option value="junk">Junk Lead</option>
                        <option value="lost">Lost Lead</option>
                        <option value="not">Not Contacted</option>
                      </select>
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Industry</div> 
                    <div className="col-md-4">
                      <select ref={(input) => this.industry = input} defaultValue={details.industry}>
                        <option value="none">None</option>
                        <option value="telecom">Telecom EOM</option>
                        <option value="erp">ERP</option>
                        <option value="govt">Government</option>
                        <option value="enterprise">Large Enterprise</option>
                        <option value="sme">Small/Medium Enterprise</option>
                      </select>
                    </div>
                </div>
              
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">No. of Employees</div> 
                    <div className="col-md-4">        
                      <input ref={(input) => this.emps = input} type="text" defaultValue={details.emps}/>
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Annual Revenue</div> 
                    <div className="col-md-4">      
                      <input ref={(input) => this.revenue = input} type="text" defaultValue={details.revenue}/>
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Skype Id</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.skype = input} type="text"  defaultValue={details.skype}/>
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Twitter Id</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.twitter = input} type="text"  defaultValue={details.twitter}/>
                    </div>
                </div>
              </div>
              <div className="clearB"></div>
            </div>
            <div className="address-info lspace">
              <div className="mDiv"><strong>Address Information</strong></div>
              <div className="floatL ltab_0">
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Address</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.address = input} type="text" defaultValue={details.address}/>
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">City</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.city = input} type="text" defaultValue={details.city}/>
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">State</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.state = input} type="text"  defaultValue={details.state}/>
                    </div>
                </div>
              </div>
              <div className="floatL ltab_1">

                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Zip Code</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.zip = input} type="text"  defaultValue={details.zip}/>
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Country</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.country = input} type="text" defaultValue={details.country} />
                    </div>
                </div>
              </div>
              <div className="clearB"></div>
                <div className="desc-info lspace">
                <div className="mDiv"><strong>Description Information</strong></div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-8 ">Description</div> 
                    <div className="col-md-8">
                      <textarea ref={(input) => this.desc = input}  defaultValue={details.desc} ></textarea>
                    </div>
                </div>
                </div>
                <div className="pull-right">
                      <ButtonToolbar>
                        <Button type="submit"  bsStyle="primary" bsSize="large">Save</Button>
                        <Button type="reset" bsStyle="warning" bsSize="large">Cancel</Button>
                      </ButtonToolbar>
                </div>
          </div>     
        </form>
    </div>
    </div>
    )
  }
}

export default LeadsEdit;



