import React from 'react';
import {Button, ButtonToolbar} from 'react-bootstrap';

class LeadsForm extends React.Component {
  createLead(event) {
    event.preventDefault();
    const lead = {
      fname: this.fname.value,
      lname: this.lname.value,
      company: this.company.value,
      email: this.email.value,
      phone: this.phone.value,
      fax: this.fax.value,
      mobile: this.mobile.value,
      website: this.website.value,
      leadsource: this.leadsource.value,
      leadstatus: this.leadstatus.value,
      industry: this.industry.value,
      emps: this.emps.value,
      revenue: this.revenue.value,
      skype: this.skype.value,
      twitter: this.twitter.value,
      address: this.address.value,
      city: this.city.value,
      state: this.state.value,
      zip: this.zip.value,
      country: this.country.value,
      desc: this.desc.value,
    }

    this.props.addLead(lead);
    //this.leadForm.reset();

  }

  render() {
    return (
      <div>
      <div className="cvBtnHeader cvpadding"  > 
        <div className="row">     
          <div className="l-list col-md-4"><h2>Create Lead</h2></div>
          <div className="l-btn col-md-4">
          </div>
        </div>
      </div>  
      <div className="lead-form">
      <div className="l-form">
      <form ref={(input) => this.leadForm = input} className="lead-new" onSubmit={(e) => this.createLead(e)}>
          <div className="pull-right">
          <ButtonToolbar>
            <Button type="submit"  bsStyle="primary" bsSize="large">Save</Button>
            <Button type="reset" bsStyle="warning" bsSize="large">Cancel</Button>
          </ButtonToolbar>
          </div>

          <div className="lspace">
            <div className="lead-info">
              <div className="mDiv"><strong>Lead Information</strong></div>
              <div className="floatL ltab_0">
                <div className="row pR tabDiv  ">
                  <div className="fixed-width col-md-4 mandatory ">First Name</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.fname = input} type="text"  style={{width: "100%"}} />
                    </div>
                </div>    
                <div className="row pR tabDiv ">
                 <div className="fixed-width col-md-4 mandatory">Last Name</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.lname = input} type="text" />
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 mandatory">Company</div> 
                    <div className="col-md-4">     
                      <input ref={(input) => this.company = input} type="text" />
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 mandatory">Email</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.email = input} type="text" />
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Phone</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.phone = input} type="text" />
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Fax</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.fax = input} type="text" />
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Mobile</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.mobile = input} type="text"  />
                    </div>
                </div>
            </div>    
              </div>
              </div>
              <div className="floatL ltab_1">
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Website</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.website = input} type="text"  />
                    </div>
                </div>

                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Lead Source</div> 
                    <div className="col-md-4">
                      <select ref={(input) => this.leadsource = input} >
                        <option value="none">None</option>
                        <option value="advertisement">Advertisement</option>
                        <option value="employee-referral">Employee Referral</option>
                        <option value="Partner">Partner</option>
                        <option value="public-relations">Public Relations</option>
                        <option value="trade-show">Trade Show</option>
                        <option value="web-download">Web Download</option>
                        <option value="web-research">Web Research</option>
                        <option value="chat">Chat</option>
                      </select>
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Lead Status</div> 
                    <div className="col-md-4">
                      <select ref={(input) => this.leadstatus = input} >
                        <option value="none">None</option>
                        <option value="attempted">Attempted to Contact</option>
                        <option value="future">Contact in Future</option>
                        <option value="contacted">Contacted</option>
                        <option value="junk">Junk Lead</option>
                        <option value="lost">Lost Lead</option>
                        <option value="not">Not Contacted</option>
                      </select>
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Industry</div> 
                    <div className="col-md-4">
                      <select ref={(input) => this.industry = input} >
                        <option value="none">None</option>
                        <option value="telecom">Telecom EOM</option>
                        <option value="erp">ERP</option>
                        <option value="govt">Government</option>
                        <option value="enterprise">Large Enterprise</option>
                        <option value="sme">Small/Medium Enterprise</option>
                      </select>
                    </div>
                </div>
              
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">No. of Employees</div> 
                    <div className="col-md-4">        
                      <input ref={(input) => this.emps = input} type="text" />
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Annual Revenue</div> 
                    <div className="col-md-4">      
                      <input ref={(input) => this.revenue = input} type="text" />
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Skype Id</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.skype = input} type="text"  />
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Twitter Id</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.twitter = input} type="text" />
                    </div>
                </div>
              </div>
              <div className="clearB"></div>
            <div className="address-info lspace">
              <div className="mDiv"><strong>Address Information</strong></div>
              <div className="floatL ltab_0">
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Address</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.address = input} type="text" />
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">City</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.city = input} type="text" />
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">State</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.state = input} type="text" />
                    </div>
                </div>
              </div>
              <div className="floatL ltab_1">

                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Zip Code</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.zip = input} type="text" />
                    </div>
                </div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-4 ">Country</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.country = input} type="text"  />
                    </div>
                </div>
              </div>
              <div className="clearB"></div>
                <div className="desc-info lspace">
                <div className="mDiv"><strong>Description Information</strong></div>
                <div className="row pR tabDiv">
                 <div className="fixed-width col-md-8 ">Description</div> 
                    <div className="col-md-8">
                      <textarea ref={(input) => this.desc = input} ></textarea>
                    </div>
                </div>
                </div>
                <div className="pull-right lspace">
                      <ButtonToolbar>
                        <Button type="submit"  bsStyle="primary" bsSize="large">Save</Button>
                        <Button type="reset" bsStyle="warning" bsSize="large">Cancel</Button>
                      </ButtonToolbar>
                </div>
          </div>     
      </form>
      </div>
      </div>
    </div>  
    )
  }
}

export default LeadsForm;



