import React from 'react';
import Contact from './Contact';
import { Link } from 'react-router-dom';
import {Button} from 'react-bootstrap';

  class ContactsList extends React.Component {

    render() {
      return( 
      <div>
      <div className="cvBtnHeader cvpadding"  > 
        <div className="row">     
          <div className="c-list col-md-4"><h2>Contacts List</h2></div>
          <div className="c-btn col-md-4"><Link to='/contacts/new'>
            <Button bsStyle="primary" className="pull-right add-contact">Add Contact</Button>
          </Link></div>
        </div>
      </div>
      <div className="contact-list">
       <div className="menu table-responsive">
        <table className="table-contact table table-hover">
        <thead>
        <tr>
          <th>Contact Name</th>
          <th>Phone</th>
          <th>Email</th>
        </tr>
        </thead>
        <tbody>
            {
              Object
                .keys(this.props.passContact())
                .map(key => <Contact key={key} index={key} details={this.props.passContact()[key]}/>)
            }
        </tbody>
        </table>
        </div>
        </div>
      </div>
      )
    } 

  }

export default ContactsList;



