import React from 'react';
import Lead from './Lead';
import { Link } from 'react-router-dom';
import {Button} from 'react-bootstrap';

  class LeadsList extends React.Component {

    render() {
      return( 
      <div className="cvBtnHeader cvpadding"  > 
        <div className="row">     
          <div className="l-list col-md-4"><h2>Leads List</h2></div>
          <div className="l-btn col-md-4"><Link to='/leads/new'>
            <Button bsStyle="primary" className="pull-right add-lead">Add Lead</Button>
          </Link></div>
        </div>
        <div className="lead-list">
          <div className="menu table-responsive">
            <table className="table-lead table table-hover">
            <thead>
            <tr>
              <th>Lead Name</th>
              <th>Company</th>
              <th>Phone</th>
              <th>Email</th>
            </tr>
            </thead>
            <tbody>
                {
                  Object
                    .keys(this.props.passLead())
                    .map(key => <Lead key={key} index={key} details={this.props.passLead()[key]}/>)
                }
            </tbody>
            </table>
          </div>
        </div>
      </div>

    )
  } 

  }

export default LeadsList;



