import React from 'react';
import {Button, ButtonToolbar} from 'react-bootstrap';

class AccountsForm extends React.Component {
  createAccount(event) {
    event.preventDefault();
    const account = {
      aname: this.aname.value,
      website: this.website.value,
      anumber: this.anumber.value,
      accounttype: this.accounttype.value,
      industry: this.industry.value,
      revenue: this.revenue.value,
      rating: this.rating.value,
      phone: this.phone.value,
      fax: this.fax.value,
      ticker: this.ticker.value,
      ownership: this.ownership.value,
      emps: this.emps.value,
      baddress: this.baddress.value,
      bcity: this.bcity.value,
      bstate: this.bstate.value,
      bzip: this.bzip.value,
      bcountry: this.bcountry.value,
      saddress: this.saddress.value,
      scity: this.scity.value,
      sstate: this.sstate.value,
      szip: this.szip.value,
      scountry: this.scountry.value,
      desc: this.desc.value,
    }

    this.props.addAccount(account);
    //this.accountForm.reset();

  }

  render() {
    return (
      <div>
        <div className="cvBtnHeader cvpadding"  > 
          <div className="row">     
            <div className="a-list col-md-4"><h2>Create Account</h2></div>
            <div className="a-btn col-md-4">
            </div>
          </div>
        </div>
      <div className="account-form">
      <div className="a-form">
      <form ref={(input) => this.accountForm = input} className="account-new" onSubmit={(e) => this.createAccount(e)}>
          <div className="pull-right">
          <ButtonToolbar>
            <Button type="submit"  bsStyle="primary" bsSize="large">Save</Button>
            <Button type="reset" bsStyle="warning" bsSize="large">Cancel</Button>
          </ButtonToolbar>
          </div>
          <div className="lspace">
            <div className="account-info">
              <div className="mDiv"><strong>Account Information</strong></div>
              <div className="floatL ltab_0">
                <div className="row pR tabDiv  ">
                  <div className="fixed-width col-md-4 mandatory ">Account Name</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.aname = input} type="text"  />
                    </div>
                </div>
                <div className="row pR tabDiv ">
                  <div className="fixed-width col-md-4 mandatory">Account Website</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.website = input} type="text" />
                    </div>
                </div>
                <div className="row pR tabDiv ">
                  <div className="fixed-width col-md-4 mandatory">Account Number</div> 
                    <div className="col-md-4">      
                      <input ref={(input) => this.anumber = input} type="text" />
                    </div>
                </div>
                <div className="row pR tabDiv ">
                  <div className="fixed-width col-md-4 mandatory">Account Type</div> 
                    <div className="col-md-4">
                      <select ref={(input) => this.accounttype = input}>
                        <option value="none">None</option>
                        <option value="analyst">Analyst</option>
                        <option value="competitor">Competitor</option>
                        <option value="customer">Customer</option>
                        <option value="distributor">Distributor</option>
                        <option value="partner">Partner</option>
                        <option value="prospect">Prospect</option>
                        <option value="press">Press</option>
                        <option value="supplier">Supplier</option>
                        <option value="vendor">Vendor</option>
                        <option value="reseller">Reseller</option>
                      </select>
                    </div>
                </div>
                <div className="row pR tabDiv ">
                 <div className="fixed-width col-md-4 ">Industry</div> 
                    <div className="col-md-4">
                      <select ref={(input) => this.industry = input}>
                        <option value="none">None</option>
                        <option value="telecom">Telecom EOM</option>
                        <option value="erp">ERP</option>
                        <option value="govt">Government</option>
                        <option value="enterprise">Large Enterprise</option>
                        <option value="sme">Small/Medium Enterprise</option>
                      </select>
                    </div>
                </div>
                <div className="row pR tabDiv ">
                  <div className="fixed-width col-md-4 ">Annual Revenue</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.revenue = input} type="text" />
                    </div>
                </div>
              </div>
              <div className="floatL ltab_1">
                <div className="row pR tabDiv ">
                  <div className="fixed-width col-md-4 ">Rating</div> 
                    <div className="col-md-4">        

                      <select ref={(input) => this.rating = input}>
                        <option value="none">None</option>
                        <option value="acquired">Acquired</option>
                        <option value="active">Active</option>
                        <option value="market-failed">Market Failed</option>
                        <option value="cancel">Project Cancelled</option>
                        <option value="shutdown">Shutdown</option>
                      </select>
                    </div>
                </div>
                <div className="row pR tabDiv ">
                  <div className="fixed-width col-md-4 ">Phone</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.phone = input} type="text"/>
                    </div>
                </div>
                <div className="row pR tabDiv ">
                  <div className="fixed-width col-md-4 ">Fax</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.fax = input} type="text"  />
                    </div>
                </div>
                <div className="row pR tabDiv ">
                  <div className="fixed-width col-md-4 ">Ticker Symbol</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.ticker = input} type="text" />   
                    </div>
                </div>
                <div className="row pR tabDiv ">
                  <div className="fixed-width col-md-4 ">Ownership</div> 
                    <div className="col-md-4">
                      <select ref={(input) => this.ownership = input}>
                        <option value="none">None</option>
                        <option value="other">Other</option>
                        <option value="private">Private</option>
                        <option value="public">Public</option>
                        <option value="subsidiary">Subsidiary</option>
                      </select>
                    </div>
                </div>
                <div className="row pR tabDiv ">
                  <div className="fixed-width col-md-4 ">No. of Employees</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.emps = input} type="text" />
                    </div>
                </div>
              </div>
            </div>
          </div>
              <div className="clearB"></div>
            <div className="address-info lspace">
              <div className="mDiv"><strong>Address Information</strong></div>
              <div className="floatL ltab_0">
                <div className="row pR tabDiv ">
                  <div className="fixed-width col-md-4 ">Billing Address</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.baddress = input} type="text"  />
                    </div>
                </div>
                <div className="row pR tabDiv ">
                  <div className="fixed-width col-md-4 ">Billing City</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.bcity = input} type="text"  />
                    </div>
                </div>
                <div className="row pR tabDiv ">
                  <div className="fixed-width col-md-4 ">Billing State</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.bstate = input} type="text"  />
                    </div>
                </div>
              

                <div className="row pR tabDiv ">
                  <div className="fixed-width col-md-4 ">Billing Zip Code</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.bzip = input} type="text"  />
                    </div>
                </div>
                <div className="row pR tabDiv ">
                  <div className="fixed-width col-md-4 ">Billing Country</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.bcountry = input} type="text" />
                    </div>
                </div>
              </div>
              <div className="floatL ltab_1">
                <div className="row pR tabDiv ">
                  <div className="fixed-width col-md-4 ">Shipping Address</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.saddress = input} type="text" />
                    </div>
                </div>
                <div className="row pR tabDiv ">
                  <div className="fixed-width col-md-4 ">Shipping City</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.scity = input} type="text" />
                    </div>
                </div>
                <div className="row pR tabDiv ">
                  <div className="fixed-width col-md-4 ">Shipping State</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.sstate = input} type="text" />
                    </div>
                </div>
                <div className="row pR tabDiv ">
                  <div className="fixed-width col-md-4 ">Shipping Zip Code</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.szip = input} type="text"  />
                    </div>
                </div>
                <div className="row pR tabDiv ">
                  <div className="fixed-width col-md-4 ">Shipping Country</div> 
                    <div className="col-md-4">
                      <input ref={(input) => this.scountry = input} type="text" />
                    </div>
                </div>
              </div>
            </div>
              <div className="clearB"></div>
                <div className="desc-info lspace">
                <div className="mDiv"><strong>Description Information</strong></div>
                <div className="row pR tabDiv ">
                  <div className="fixed-width col-md-4 ">Description</div> 
                    <div className="col-md-4">
                      <textarea ref={(input) => this.desc = input} ></textarea>
                    </div>
                </div>
              </div>

              <div className="pull-right">
                <ButtonToolbar>
                  <Button type="submit"  bsStyle="primary" bsSize="large">Save</Button>
                  <Button type="reset" bsStyle="warning" bsSize="large">Cancel</Button>
                </ButtonToolbar>
              </div>
          
      </form>
      </div>
      </div>
    </div>
    )
  }
}

export default AccountsForm;



