import React from 'react';
import { Button} from 'react-bootstrap';
import FontAwesome from 'react-fontawesome';
import { Link } from 'react-router-dom';

import '../App.css';



class AccountDetails extends React.Component {

	render() {
		const {details} = this.props;
		return (
			<div>
			<Link to="/accounts">
			<FontAwesome name="arrow-left" />
			</Link>

			<div className="account-details container-fluid">
				<div className="pull-right">
					<Button  bsSize="large">Edit</Button>
				</div>
					<div className="accdetails-name">
						<strong>{details.aname}</strong> - <a>{details.website}</a>
					</div>
				<div className="clearB"></div>
				
				<div>
					<div className="row mDiv">
						<div className="fixed-width col-md-4">Account Owner</div> 
						<div className="col-md-4">Praveen Kumar</div>
					</div>
					<div className="row mDiv">
						<div className="fixed-width col-md-4">Industry</div> 
						<div className="col-md-4">{details.industry}</div>
					</div>
					<div className="row mDiv">
						<div className="fixed-width col-md-4">No. of Employees</div> 
						<div className="col-md-4">{details.emps}</div>
					</div>
					<div className="row mDiv">
						<div className="fixed-width col-md-4">Annual Revenue</div> 
						<div className="col-md-4">{details.revenue}</div>
					</div>
					<div className="row mDiv">
						<div className="fixed-width col-md-4">Phone</div> 
						<div className="col-md-4">{details.phone}</div>
					</div>
					
					<div className="acontact-info lspace">
						<div className="mDiv"><strong>Contacts</strong></div>
						<div className="row pR mDiv">
							<div className="fixed-width col-md-4 ">Email</div>
							<div className="col-md-4">{details.email}</div>
						</div>
						<div className="row pR mDiv">
							<div className="fixed-width col-md-4 ">Phone</div>
							<div className="col-md-4">{details.phone}</div>
						</div>
						<div className="row pR mDiv">
							<div className="fixed-width col-md-4 ">Mobile</div>
							<div className="col-md-4">{details.mobile}</div>
						</div>
						<div className="row pR mDiv">
							<div className="fixed-width col-md-4 ">Account Status</div>
							<div className="col-md-4">{details.Accountstatus}</div>
						</div>
					</div>
				</div>
				<div className="account-info lspace">
					<div className="mDiv"><strong>Account Information</strong></div>
					<div className="floatL ltab_0">
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Account Owner</div>
							<div className="col-md-4">Praveen Kumar</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Account Name</div>
							<div className="col-md-4">{details.aname}</div>
						</div>
						<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Account Website</div>
								<div className="col-md-4">{details.website}</div>
						</div>
						<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Account Number</div>
								<div className="col-md-4">{details.anumber}</div>
						</div>
						<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Account Type</div>
								<div className="col-md-4">{details.accounttype}</div>
						</div>
						<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Industry</div>
								<div className="col-md-4">{details.industry}</div>
						</div>
						<div className="row pR tabDiv">
								<div className="fixed-width col-md-4 ">Annual Revenue</div>
								<div className="col-md-4">{details.revenue}</div>
						</div>
					</div>
					<div className="floatL ltab_1">
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Rating</div>
							<div className="col-md-4">{details.rating}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Phone</div>
							<div className="col-md-4">{details.phone}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Fax</div>
							<div className="col-md-4">{details.fax}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Website</div>
							<div className="col-md-4">{details.website}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Ticker Symbol</div>
							<div className="col-md-4">{details.ticker}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Ownership</div>
							<div className="col-md-4">{details.ownership}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">No. of Employees</div>
							<div className="col-md-4">{details.emps}</div>
						</div>
					</div>
					
				</div>
				<div className="clearB"></div>

				<div className="address-info lspace">
					<div className="mDiv"><strong>Address Information</strong></div>
					<div className="floatL ltab_0">
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Billing Address</div>
							<div className="col-md-4">{details.baddress}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Billing State</div>
							<div className="col-md-4">{details.bstate}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Billing City</div>
							<div className="col-md-4">{details.bcity}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Billing Zip Code</div>
							<div className="col-md-4">{details.bzip}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Billing Country</div>
							<div className="col-md-4">{details.bcountry}</div>
						</div>
					</div>
					<div className="floatL ltab_1">
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Shipping Address</div>
							<div className="col-md-4">{details.saddress}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Shipping State</div>
							<div className="col-md-4">{details.sstate}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Shipping City</div>
							<div className="col-md-4">{details.scity}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Shipping Zip Code</div>
							<div className="col-md-4">{details.szip}</div>
						</div>
						<div className="row pR tabDiv">
							<div className="fixed-width col-md-4 ">Shipping Country</div>
							<div className="col-md-4">{details.scountry}</div>
						</div>
					</div>
				</div>
				<div className="clearB"></div>

				<div>
					<div className="desc-info lspace"><strong>Description Information</strong></div>
					<div className="row pR tabDiv">
						<div className="fixed-width col-md-4 ">Description</div>
						<div className="col-md-4">{details.desc}</div>
					</div>
				</div>
			</div>
			</div>
		)
	}
}

export default AccountDetails ;


