import React from 'react';
import { Nav, Navbar, NavItem } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';

class Header extends React.Component {

	render() {
		return (<div>
			<Navbar inverse collapseOnSelect className="container-fluid">
				<LinkContainer to="/">
					<Navbar.Header>
					<Navbar.Brand><strong>React-CRM App</strong></Navbar.Brand>
					<Navbar.Toggle />
					</Navbar.Header>
				</LinkContainer>
				<Navbar.Collapse>
		      	<Nav>
			   	<LinkContainer to="/home"><NavItem>Home</NavItem></LinkContainer>
			   	<LinkContainer to="/leads"><NavItem>Leads</NavItem></LinkContainer>
			   	<LinkContainer to="/accounts"><NavItem>Accounts</NavItem></LinkContainer>
			   	<LinkContainer to="/contacts"><NavItem>Contacts</NavItem></LinkContainer>
			   	<LinkContainer to="/potentials"><NavItem>Potentials</NavItem></LinkContainer>
				</Nav>
				</Navbar.Collapse>
			</Navbar>
			</div>

		)
	}
}

export default Header ;


