import React from 'react';
import Account from './Account';
import { Link } from 'react-router-dom';
import { Button } from 'react-bootstrap';

  class AccountsList extends React.Component {

    render() {
      return( 
       <div> 
       <div className="cvBtnHeader cvpadding"  > 
        <div className="row">     
          <div className="a-list col-md-4"><h2>Accounts List</h2></div>
          <div className="a-btn col-md-4"><Link to='/accounts/new'>
            <Button bsStyle="primary" className="pull-right add-account">Add Account</Button>
          </Link></div>
        </div>
      </div> 
      <div className="account-list">
       <div className="menu table-responsive">
        <table className="table-account table table-hover">
        <thead>
        <tr>
          <th>Account Name</th>
          <th>Phone</th>
          <th>Website</th>
        </tr>
        </thead>
        <tbody>
            {
              Object
                .keys(this.props.passAccount())
                .map(key => <Account key={key} index={key} details={this.props.passAccount()[key]}/>)
            }
        </tbody>
        </table>
        </div>
      </div>
      </div>
      )
    } 

  }

export default AccountsList;



